.searchWrap {
  width: 80%;
  max-width: 600px;
  :global {
    .ant-input-group {
      box-shadow: 0 4px 16px -4px #000000;
      border-radius: 8px;
      .ant-input-group-addon {
        border: none;
        background-color: #fff;
      }
      .ant-input-borderless {
        background-color: #fff;
      }
    }
  }
}
