.main {
  display: flex;
  height: 100vh;
  width: 100%;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.main > section {
  text-align: center;
  color: var(--title-color);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  position: relative;
  & > div { width: 100%; }
}
